@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/earlyaccess/nats.css);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400&display=swap);
body {
  margin: 0;
  font-family: Noto Sans KR, Righteous, -apple-system, NATS, BlinkMacSystemFont,
    Segoe UI, Roboto, sans-serif, Helvetica Neue, Arial, Apple Color Emoji,
    Segoe UI Emoji;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  width: 100%;
  height: 100%;
  position: fixed;
  overflow: hidden;
  touch-action: none;
  margin: 0;
  padding: 0;
  @media (min-width: 768px) {
    font-size: 16px;
  }
}

code {
  font-family: Righteous, NATS, source-code-pro, Menlo, Monaco, Consolas,
    "Courier New", monospace;
}

.IntroItem {
  position: relative;
  width: 100%;
  height: 23vh;
  margin-top: 1vh;
  margin-bottom: 1vh;
  padding-bottom: 1vh;
  padding-top: 1vh;
}

.IntroItem_day {
  position: absolute;
  top: 2vh;
  width: auto;
  height: 1vh;
  right: 0;
  margin-right: 3vw;
  font-family: NATS;
  font-style: normal;
  font-weight: 600;
  font-size: 5vh;
  line-height: 4.5vh;
  color: #033540;
}

.IntroItem_content {
  position: absolute;
  display: inline-block;
  height: auto;
  width: auto;
  white-space: pre-wrap;
  background: #4ba4f2;
  box-shadow: 0px 0.5vh 0.5vh rgba(0, 0, 0, 0.2);
  border-radius: 1.8vw;
  top: 7vh;
  right: 0;
  font-family: NATS;
  font-style: normal;
  font-weight: 500;
  font-size: 2.8vh;
  line-height: 4vh;
  right: 0;
  margin-right: 2vw;
  margin-left: 2vw;
  padding: 3vh 3vw;
  /* or 125% */
  text-align: right;
  color: #f2f2f2;
}

.ProjectItem {
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  position: relative;
  width: 27vw;
  height: 33vh;
  background: #0468bf;
  border-radius: 2vw;
  margin: 1vw;
  margin-bottom: 4vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 1px 3px 2px rgba(0, 0, 0, 0.25);
}

.ProjectItem_img {
  position: absolute;
  background: #e4e4e4;
  left: 0;
  box-shadow: 1px 3px 2px rgba(0, 0, 0, 0.25);
  top: 0;
  width: 100%;
  object-fit: cover;
  height: 70%;
}

.ProjectItem_day {
  position: absolute;
  bottom: 6vh;
  font-weight: bord;
  font-family: Noto Sans KR, sans-serif;
  font-size: 2.5vh;
  line-height: 2.5vh;
  color: white;
}

.ProjectItem_content {
  position: absolute;
  bottom: 2.5vh;
  font-family: Noto Sans KR, sans-serif;
  color: white;
  font-size: 1vw;
  line-height: 1.5vh;
}

.SkillItem {
  position: relative;
  width: 90%;
  height: 10vh;
  padding-top: 3vh;
  padding-bottom: 2vh;
}

.SkillItem_collect {
  position: absolute;
  right: 0;
  width: 70%;
  height: 100%;
}
.SkillItem_name {
  position: absolute;
  top: 1vh;
  margin-left: 10px;
  position: absolute;
  width: 87%;
  height: 5vh;
  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  font-size: 1.7vw;
  line-height: 2.5vw;
  color: #000000;
}

.SkillItem_pro {
  position: absolute;
  width: auto;
  height: auto;
  right: 0;
  top: 3vh;
  text-align: right;
  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  font-size: 2.5vh;
  line-height: 2.5vh;
  color: #000000;
}

@media screen and (max-width: 950px) {
  .SkillItem {
    position: relative;
    width: 90%;
    height: 10vh;
    padding-top: 3vh;
    padding-bottom: 2vh;
  }

  .SkillItem_collect {
    position: absolute;
    right: 0;
    width: 70%;
    height: 100%;
  }
  .SkillItem_name {
    font-size: 3vw;
    line-height: 4vw;
  }

  .SkillItem_pro {
    font-size: 2vw;
    line-height: 3vw;
  }

  .ProjectItem {
    width: 60vw;
    height: 35vh;
    border-radius: 2vh;
    margin-top: 6vh;
    margin-bottom: 6vh;
  }

  .ProjectItem_day {
    bottom: 6vh;
    font-size: 3vh;
  }

  .ProjectItem_content {
    bottom: 2.5vh;
    font-size: 2vh;
  }
}

@media screen and (max-width: 750px) {
  .SkillItem_collect {
    height: 10vh;
    width: 55vw;
  }
  .SkillItem {
    position: relative;
    width: 90%;
    height: 13vw;
    padding-top: 2vh;
    padding-bottom: 2vh;
  }

  .SkillItem_name {
    top: 2vw;
    font-size: 5vw;
    line-height: 4vw;
  }

  .SkillItem_pro {
    top: 4vw;
    font-size: 2.5vw;
    line-height: 3vw;
    margin-left: 2vw;
  }

  .ProjectItem {
    width: 70vw;
    height: 27vh;
    border-radius: 2vh;
    margin-top: 6vh;
    margin-bottom: 6vh;
  }

  .ProjectItem_day {
    bottom: 4.5vh;
    font-size: 2.5vh;
  }

  .ProjectItem_content {
    bottom: 1.8vh;
    font-size: 1.5vh;
  }
}

.IntroItem_day {
  position: absolute;
  top: 2vh;
  width: auto;
  height: 1vh;
  right: 0;
  margin-right: 3vw;
  font-family: NATS;
  font-style: normal;
  font-weight: 600;
  font-size: 5vh;
  line-height: 4.5vh;
  color: #033540;
}

@media screen and (max-width: 550px) {
  .IntroItem_content {
    font-size: 3vw;
    line-height: 6vw;
    padding: 3vh 5vw;
    border-radius: 3vw;
  }
}

@media screen and (max-width: 499px) {
  .ProjectItem {
    width: 70vw;
    height: 25vh;
    border-radius: 4vh;
  }

  .ProjectItem_day {
    bottom: 4.2vh;
    font-size: 2.3vh;
  }

  .ProjectItem_content {
    bottom: 1.8vh;
    font-size: 1.5vh;
  }

  .IntroItem {
    position: relative;
    width: 95%;
    height: 18vh;
    margin-top: 1vh;
    margin-bottom: 1vh;
    padding-bottom: 1vh;
    padding-top: 1vh;
    margin-left: 3vw;
  }

  .IntroItem_content {
    font-size: 3.8vw;
    line-height: 7vw;
    padding: 2vh 3vw;
  }
}

.SkillButton_div {
  position: absolute;
  width: auto;
  height: auto;
  bottom: 3.5vh;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.SkillButton {
  position: relative;
  background: azure;
}

.ProjectTopDiv {
  position: relative;
  width: 100%;
  height: 28vh;
  margin-top: 10vh;
  margin-bottom: 20px;
}

.ProjectHeaderText {
  position: relative;
  left: 5%;
  height: 12vh;
  font-family: NATS;
  font-style: normal;
  font-weight: bolder;
  font-size: 7vh;
  color: #000000;
}

.ProjectLinkText {
  z-index: 3;
  font-family: NATS;
  height: auto;
  font-style: normal;
  font-weight: normal;
  font-size: 3.5vh;
  text-align: right;
  color: #5c5c5c;
  margin-bottom: 13vh;
  text-decoration: underline;
  text-underline-position: under;
}

@media screen and (max-width: 750px) {
  .ProjectTopDiv {
    position: relative;
    width: 100%;
    height: 25vh;
    margin-top: 10vh;
  }

  .ProjectHeaderText {
    height: 6vh;
    font-size: 5vh;
    margin-bottom: 3vh;
    left: -3vw;
  }

  .ProjectLinkText {
    font-size: 4vw;
  }
}

@media screen and (max-width: 499px) {
  .ProjectTopDiv {
    height: 20vh;
  }

  .ProjectLinkText {
    font-size: 5vw;
    line-height: 8vw;
  }
}

.Homeimg {
