.navbar {
  display: flex;
  width: 100%;
  height: 9vh;
  position: fixed;
  background: #ffffff;
  box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.25);
  padding: 5px 2vw;
  color: black;
  align-items: center;
  z-index: 2;
}

.nav_Text {
  position: absolute;
  font-family: Righteous;
  font-style: normal;
  width: 50%;
  height: auto;
  font-weight: normal;
  font-size: 4vh;
  color: #000000;
}

.nav_menu {
  display: flex;
  justify-content: flex-end;
  position: absolute;
  right: 5vw;
  height: auto;
  width: auto;
  top: 0;
  font-family: NATS;
  font-size: 4vh;
  color: #000000;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding: 0;
  display: inline-block;
}

.nav_menu a {
  text-decoration: none;
  color: black;
}
.nav_menu ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav_menu li {
  padding: 2vh 1vw;
  float: left;
}

.nav_menu li:hover {
  color: #035aa6;
}

@media screen and (max-width: 750px) {
  .navbar {
    position: fixed;
    width: 80vw;
    height: 45vh;
    top: 3vh;
    overflow: hidden;
    background: #0468bf;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    padding: 1vw;
    border-radius: 5vw;
    font-family: NATS;
    z-index: 4;
    font-size: 3vh;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  .nav_Text {
    position: absolute;
    font-family: Righteous;
    font-style: normal;
    font-weight: normal;
    top: 2vh;
    left: 5vw;
    height: 10%;
    width: 70%;
    font-size: 3.5vh;
    color: white;
  }
  .nav_menu {
    position: absolute;
    height: 80%;
    bottom: 0;
    left: 0;
    color: #000000;
    width: 100%;
    display: block;
  }

  .nav_menu ul {
    background-color: white;
    position: absolute;
    text-align: right;
    top: 4vh;
    width: 100%;
    height: 105%;
    font-size: 3vh;
  }
  .nav_menu li {
    font-family: NATS;
    font-style: normal;
    font-weight: 400;
    margin-right: 2vw;
    line-height: 2vh;
    height: 5vh;
    float: none;
    font-size: 4vh;
  }
}

